$primaryColor: #ffffff;
$primaryHover: darken($primaryColor, 40%);

$secondaryColor: #1f1f1f;
$secondaryColorHover: darken($secondaryColor, 80%);

$whiteThemeText: #aaa;

$RED: #ff4444;
$BLUE: #56b6c2;
$GREEN: #98c379;
$YELLOW: #e5c07b;
$PINK: #c678dd;
$GRAY: #5c6370;

$userNameColor: #727272;

$bgCard: #2a2a32;
$bgWhite: #1d1d23;
$bgWhitePage: linear-gradient(to right, $bgCard, darken($bgCard, 10%));
$bgCategories: linear-gradient(90deg, $GREEN, darken($GREEN, 20%));
$bgMenuMobile: linear-gradient(90deg, #1d1926, #252525);
$bgGrad: linear-gradient(50deg, $bgCard, darken($bgCard, 10%));